const swiper = new Swiper('.mySwiper', {
    // Optional parameters
    direction: 'horizontal',
    loop: true,

    // If we need pagination
    pagination: {
        el: '.promotion_box .swiper-pagination',
    },

    // Navigation arrows
    navigation: {
        nextEl: '.promotion_box .swiper-button-next',
        prevEl: '.promotion_box .swiper-button-prev',
    },

    // And if we need scrollbar
    scrollbar: {
        el: '.promotion_box .swiper-scrollbar',
    },
});

const reviews = new Swiper('.reviewsSwiper', {
    // Optional parameters
    direction: 'horizontal',
    loop: true,
    slidesPerView: 1,
    spaceBetween: 24,
    breakpoints: {
        // when window width is >= 320px
        320: {
            slidesPerView: "auto",
            spaceBetween: 24
        },
        // when window width is >= 480px
        480: {
            slidesPerView: 1,
            spaceBetween: 24
        },
        // when window width is >= 640px
        640: {
            slidesPerView: 3,
            spaceBetween: 24
        }
    },

    // If we need pagination
    pagination: {
        el: '.reviews_box .swiper-pagination',
    },

    // Navigation arrows
    navigation: {
        nextEl: '.reviews_box .swiper-button-next',
        prevEl: '.reviews_box .swiper-button-prev',
    },

    // And if we need scrollbar
    scrollbar: {
        el: '.reviews_box .swiper-scrollbar',
    },


});

