document.addEventListener("DOMContentLoaded", function(event) { // <-- add this wrapper
    var element = document.querySelectorAll('.faq_item-el');
    var hmn_items = document.querySelectorAll('.hmn_item');
    var slist_btn = document.querySelector('.show_service_list');
    var slist_close = document.querySelector('.svg_arrow_close');
    var slist = document.querySelector('.service_list');

    if (hmn_items) {
        hmn_items.forEach(function(el, key){
            el.addEventListener('click', function () {
                el.classList.toggle("active");
                /*const hasActiveClass = element.classList.contains('active');

                if (hasActiveClass) {
                    parentNode.classList.add('active');
                }*/

                element.forEach(function(ell, els){
                    if(key !== els) {
                        ell.classList.remove('active');
                    }
                });
            });
        });
    }
    if (element) {
        element.forEach(function(el, key){
            el.addEventListener('click', function () {
                el.classList.toggle("active");
                /*const hasActiveClass = element.classList.contains('active');

                if (hasActiveClass) {
                    parentNode.classList.add('active');
                }*/

                element.forEach(function(ell, els){
                    if(key !== els) {
                        ell.classList.remove('active');
                    }
                });
            });
        });
    }
    if (slist) {
        slist_btn.addEventListener('click', function () {
            slist_btn.classList.toggle("active");
            slist.classList.toggle("active");
        });
    }
    if (slist_close) {
        slist_close.addEventListener('click', function () {
            slist_btn.classList.remove("active");
            slist.classList.remove("active");
        });
    }
});
